<template>
    <div class="logAudit">
        <operationPanel>
        <info-from slot="left" :config="config" :modelData="parameter"/>
        <div slot="right" class="operationListBtn">
            <a :href="`/api/logs/metrics/request/export?loginId=${parameter.loginId}&startDate=${parameter.startDate}&endDate=${parameter.endDate}&x-auth-token=${token}`">
                <el-button type="text" icon="el-icon-search" >导出</el-button>
            </a>
        </div>
        </operationPanel>
        <div class="list">
            <table-btn
                :columns="columns"
                :list="list"
                :options="tableOption"
            />
        </div>
        <panel title="">
        <pagination
            :totalNumber="totalNumber"
            :parameter="parameter"
            @changeTableData="changeTableData"
        />
        </panel>
    </div>
</template>
<script>
import moment from "moment"//时间格式化
import operationPanel from "@components/layout/operationPanel";
import infoFrom from "@components/infoFrom/infoFrom";
import tableBtn from "@components/table/tableList-btn";
import Pagination from "@components/pagination/pagination";
import Panel from "@components/layout/panel";
import {
  queryObject,
  createParameter
} from "@components/function/queryParameter.js";
export default {
  components: {
    operationPanel,
    infoFrom,
    tableBtn,
    Pagination,
    Panel
  },
  data () {
    return {
        token: sessionStorage["x-auth-token"],
        tableOption: {
            height: 'calc(100vh - 210px)'
        },
        config: {
            ref: "ruleForm",
            size: "small",
            lableWidth: "80px",
            inline: true,
            rule: [
                {
                    label: "用户",
                    type: "infoInput",
                    model: "loginId",
                    methods: val => {
                    this.parameter.loginId = val
                    },
                    button: {
                        slot: 'append',
                        icon: 'el-icon-search',
                        methods: () => {
                            // 请求 table 数据
                            this.parameter.limit = 8
                            this.parameter.offset = 0
                            this.queryTableData()
                        }
                    }
                },
                {
                    label: "时间",
                    type: "infoDatePicker",
                    valueFormat: 'yyyy-MM-dd',
                    model: 'startDate, endDate',
                    methods: val => {
                        this.parameter.startDate = val[0]
                        this.parameter.endDate = val[1]
                        this.parameter.limit = 8
                        this.parameter.offset = 0
                        // 请求 table 数据
                        this.queryTableData()
                    }
                }
            ]
        },
        parameter: {
            limit: 8,
            offset:0,
            loginId: '',
            startDate: '',
            endDate: ''
        }, // 分页请求数据
        columns: [
            // 需要展示的列
            {
                prop: "write_time",
                label: "操作时间",
                width: 250,
                formatter: (row, column) => {
                    return moment(Number(row['write_time'])).format("YYYY-MM-DD HH:mm:ss");
                }
            },{
                prop: "clientIp",
                label: "IP",
                width: 150,
            },{
                prop: "loginId",
                label: "操作人",
                width: 120,
            },{
                prop: "operation",
                label: "操作动作",
                width: 300,
            },{
                prop: "params",
                label: "操作内容",
                formatter: (row, column) => {
                    return `<span style=" display: block; overflow: hidden;white-space:nowrap;" >${row.params}</span>`
                },
            }
        ],
        list: [],
        totalNumber: 0,
    }
  },
  methods: {
    // 分页请求数据的回调
    changeTableData (data) {
        this.$set(this, 'parameter', data)
        this.queryTableData()
    },
    // 请求 table 列表
    queryTableData () {
        this.$store.commit('updateParameter', {
            data: this.parameter
        })
        this.serverApi({
        params: this.parameter,
        interface: 'logsMetrics',
        success: (response) => {
            this.list = response.content
            this.totalNumber = response.totalElements
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
    .logAudit {
        .list {
            height: 100%;
            width: 100%;
        }
    }
</style>